import React, { useState,useEffect } from "react";
import { useNavigate } from 'react-router-dom';
import styles from './style.module.scss'
import { SearchBar } from "components";
import { numberWithCommas } from "utils/Commas";
import useOrderList from "hooks/useOrderList";

 
function OrderListContent() {
  const navigate = useNavigate();
  const [filter,setFilter] = useState({
    category:{ 
      ALL : { checked : true},
      QR : { checked : true},
      laser : { checked : true},
      print : { checked : true},
      embroidery : { checked : true},
    }}
  );
  const [list, setList] = useOrderList();
  const [tabIndex,setTabIndex] = useState(0);

  useEffect(()=>{
    setList((l) => { 
      l.map((value)=> { 
        if(filter.category)
          value.isHide = !(value.category === filter.category);
        else
          value.isHide = false;
      })
      return [...l]
    })
  },[filter])

  const LinkClick = (value,path) => {
    return navigate(`/order/detail/${value.type}/${path}`,{state:value});
  }

  const activeStyle = (type) => {
    const ORDER_STATE ={"order": {color:"#2266AB"},"paid":{color:"#22AB2E"},"wip":{color:"#5C22AB"},"done":{color:"#AB3222"},"taken":{color:"#AB3222"}};
    return ORDER_STATE[type] ? ORDER_STATE[type].color : "#2266AB";
  }
  const detailTitle = (type) => {
    const ORDER_STATE ={"QR": {title:"PHOTO SCAN PRINTING"},"print":{title:"NBY DIGITAL PRINTING"},"laser":{title:"LASER GUN PRINTING"},"embroidery":{title:"EMBROIDERY"}};
    return ORDER_STATE[type] ? ORDER_STATE[type].title : "PHOTO SCAN PRINTING";
  }

  function getListLength(paramList){
    return paramList && paramList.filter(v=>((filter.category[v.category] && filter.category[v.category].checked) || filter.category["ALL"].checked)).length;
  }

  return (
    <div className={styles.Container}>
      <SearchBar tabIndex={tabIndex} setTabIndex={setTabIndex} filter={filter} setFilter={setFilter}/>
      <div className={styles.OrderListWrap}>
        <div className={styles.OrderInner}>
          <div className={styles.OrderBox}>
            <p className={styles.OrderTitle}>작업대기<span>{getListLength(list.filter(v=>v.status === "order"))}개</span></p>
            <div className={styles.ListWrap}>
              {
                list.filter(v=>v.status === "order" && ((filter.category[v.category] && filter.category[v.category].checked) || filter.category["ALL"].checked)).map((value,index)=>{
                  return(
                    <div className={styles.ListBox}>
                      <div style={{backgroundColor: activeStyle(value.type)}} className={styles.ListTitleBox}>
                        <p>{detailTitle(value.category)}</p> <span>{value.date}</span>
                      </div>
                      <div className={styles.ListContent}>
                        <p>NAME : {value.name}</p>
                        <p>PHONE : {value.phone}</p>
                        <p>총 결제 금액 : <span>{numberWithCommas(value.price)}원</span></p>
                        <button onClick={()=>{LinkClick(value,value.id)}}>자세히 보기</button>
                      </div>
                    </div>
                  );
                  
                })
              }
            </div>
          </div>
          <div className={styles.OrderBox}>
            <p className={styles.OrderTitle}>작업중<span>{getListLength(list.filter(v=>v.status === "wip"))}개</span></p>
            <div className={styles.ListWrap}>
              {
                list.filter(v=>v.status === "wip").map((value,index)=>{
                  if(value.isHide)
                  return(
                    <div className={styles.ListBox}>
                      <div style={{backgroundColor: activeStyle(value.type)}} className={styles.ListTitleBox}>
                        <p>{detailTitle(value.type)}</p> <span>{value.date}</span>
                      </div>
                      <div className={styles.ListContent}>
                        <p>NAME : {value.name}</p>
                        <p>PHONE : {value.phone}</p>
                        <p>총 결제 금액 : <span>{numberWithCommas(value.price)}원</span></p>
                        <button onClick={()=>{LinkClick(value,value.id)}}>자세히 보기</button>
                      </div>
                    </div>
                  )
                })
              }
            </div>
          </div>
          <div className={styles.OrderBox}>
            <p className={styles.OrderTitle}>픽업대기<span>{getListLength(list.filter(v=>v.status === "done"))}개</span></p>
            <div className={styles.ListWrap}>
              {
                list.filter(v=>v.status === "done").map((value,index)=>{
                  if(value.isHide)
                  return(
                    <div className={styles.ListBox}>
                      <div style={{backgroundColor: activeStyle(value.type)}} className={styles.ListTitleBox}>
                        <p>{detailTitle(value.type)}</p> <span>{value.date}</span>
                      </div>
                      <div className={styles.ListContent}>
                        <p>NAME : {value.name}</p>
                        <p>PHONE : {value.phone}</p>
                        <p>총 결제 금액 : <span>{numberWithCommas(value.price)}원</span></p>
                        <button onClick={()=>{LinkClick(value,value.id)}}>자세히 보기</button>
                      </div>
                    </div>
                  )
                })
              }
            </div>
          </div>
          <div className={styles.OrderBox}>
            <p className={styles.OrderTitle}>결제완료<span>{getListLength(list.filter(v=>v.status === "paid"))}개</span></p>
            <div className={styles.ListWrap}>
              {
                list.filter(v=>v.status === "paid").map((value,index)=>{
                  if(value.isHide)
                  return(
                    <div className={styles.ListBox}>
                      <div style={{backgroundColor: activeStyle(value.type)}} className={styles.ListTitleBox}>
                        <p>{detailTitle(value.type)}</p> <span>{value.date}</span>
                      </div>
                      <div className={styles.ListContent}>
                        <p>NAME : {value.name}</p>
                        <p>PHONE : {value.phone}</p>
                        <p>총 결제 금액 : <span>{numberWithCommas(value.price)}원</span></p>
                        <button onClick={()=>{LinkClick(value,value.id)}}>자세히 보기</button>
                      </div>
                    </div>
                  )
                })
              }
            </div>
          </div>
          <div className={styles.OrderBox}>
            <p className={styles.OrderTitle}>주문취소<span>{getListLength(list.filter(v=>v.status === "wip"))}개</span></p>
            <div className={styles.ListWrap}>
              {
                list.filter(v=>v.status === "wip").map((value,index)=>{
                  if(value.isHide)
                  return(
                    <div className={styles.ListBox}>
                      <div style={{backgroundColor: activeStyle(value.type)}} className={styles.ListTitleBox}>
                        <p>{detailTitle(value.type)}</p> <span>{value.date}</span>
                      </div>
                      <div className={styles.ListContent}>
                        <p>NAME : {value.name}</p>
                        <p>PHONE : {value.phone}</p>
                        <p>총 결제 금액 : <span>{numberWithCommas(value.price)}원</span></p>
                        <button onClick={()=>{LinkClick(value,value.id)}}>자세히 보기</button>
                      </div>
                    </div>
                  )
                })
              }
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default OrderListContent;
