import React, { useEffect } from "react";
import { useNavigate,useParams,useLocation } from 'react-router-dom';
import styles from './style.module.scss'
import { Button } from "components";

// img
import IconBack from 'resources/icon/icon_back.svg'
import GuideImg from 'resources/image/guide_img.png'
import GuideScanImg from 'resources/image/guide_scan_img.png'
import GuideDrawingImg from 'resources/image/guide_drawing_img.png'
import API from "api";

function OrderDetailContent() {
  const navigate = useNavigate();
  const { type } = useParams();
  const location = useLocation();
  const state = location.state;

  useEffect(()=>{
    loadOption();
  },[]);

  const loadOption = async () => {
    let options =  await API.getOptions();
    if(options && options.option_list){

    }
  }

  const LinkClick = (path) => {
    return navigate(path);
  }

  const activeStyle = (type) => {
    if(type == 0){return "#2266AB";}
    if(type == 1){return "#22AB2E"}
    if(type == 2){return "#5C22AB"}
    if(type == 3){return "#AB3222"}
  }
  const detailTitle = (type) => {
    if(type == 0){return "PHOTO SCAN PRINTING";}
    if(type == 1){return "NBY DIGITAL PRINTING"}
    if(type == 2){return "EMBROIDERY"}
    if(type == 3){return "LASER GUN PRINTING"}
  }
  function dataURLtoBlob(dataurl) {
    var arr = dataurl.split(','),
      mime = arr[0].match(/:(.*?);/)[1],
      bstr = atob(arr[1]),
      n = bstr.length,
      u8arr = new Uint8Array(n);
    while (n--) {
      u8arr[n] = bstr.charCodeAt(n);
    }
    return new Blob([u8arr], {
      type: mime
    });
  }
  
  const onDownloadImage = () => {
    var image = new Image();
    image.crossOrigin = "anonymous";
    image.src = state.img && state.img[0] == "h" ? state.img : "" + state.img;
    var fileName = image.src.split("/").pop();
    image.onload = function() {
      var canvas = document.createElement('canvas');
      canvas.width = this.width;
      canvas.height = this.height;
      canvas.getContext('2d').drawImage(this, 0, 0);
      if (typeof window.navigator.msSaveBlob !== 'undefined') {
        window.navigator.msSaveBlob(dataURLtoBlob(canvas.toDataURL()), fileName);
      } else {
        var link = document.createElement('a');
        link.href = canvas.toDataURL();
        link.download = fileName;
        link.click();
      }
    };
  }

  function getTotalListPrice(list){
    let price = 0;
    list.map((value)=>{
      price += value.price;
    })
    return price;
  }

  function getTotalPrice(){
    const categoryPrice = { QR : 5000, print : 15000, embroidery : 3000,laser:3000,laser_pre : 3000};
    let price = getTotalListPrice(state.assets ? state.assets : []);
    price += categoryPrice[state.category];
    return price;
  }

  function getOrderDate(){
    let date = new Date(state.modified_at.replace("T"," ").split(".")[0]);
    function zeroCheck(num){
      return num < 10 ? "0"+num : num;
    }
    return date.getFullYear().toString().slice(2,4) + "." + zeroCheck(date.getMonth()) + "." + zeroCheck(date.getDay()) + " / "
    + (date.getHours() >= 12 ? "PM " : "AM ") + zeroCheck(date.getHours() >= 12 ? date.getHours() - 12 : date.getHours()) + ":" + zeroCheck(date.getMinutes());
  }

  return (
    <div className={styles.Container}>
      <div style={{backgroundColor: activeStyle(type)}} className={styles.DetailTitleBox}>
        <p>{detailTitle(type)}</p>
        <img src={IconBack} alt="뒤로가기" onClick={()=>{LinkClick(-1)}}/>
      </div>

      <div className={styles.DetailWrap}>
        <div className={styles.DetailImgWrap}> 
          <p className={styles.DetailTitle}>작업 이미지</p>
          <div className={styles.Imgbox}>
            <img src={state.img && state.img[0] == "h" ? state.img : "" + state.img} alt="" />
            <button onClick={onDownloadImage}>작업 이미지 다운로드</button>
          </div>
        </div>

        <div className={styles.DetailInfoWrap}>
          <div className={styles.DetailInfoBox}>
            <p className={styles.DetailTitle}>주문자 정보</p>
            <div className={styles.InfoContent}>
              <p>NAME : {state.name}</p>
              <p>PHONE : {state.phone}</p>
            </div>
          </div>
          <div className={styles.DetailInfoBox}>
            <p className={styles.DetailTitle}>주문 날짜</p>
            <div className={styles.InfoContent}>
              <p>{getOrderDate()}</p>
            </div>
          </div>
          <div className={styles.DetailInfoFullBox}>
            <p className={styles.DetailTitle}>주문 내용</p>
            <div className={styles.InfoContent}>
              <p>제품명 : <span>0000후드</span></p>
              <p>컬러 : <span><i style={{backgroundColor: "#000000"}}></i> 블랙</span></p>
              <p>사이즈 : <span>S</span></p>
              <p>가격 : <span>10,000원</span></p>
            </div>
          </div>
          <div className={styles.DetailInfoBox}>
            <p className={styles.DetailSubTitle}>포토 스캔 프린트<span>{getTotalListPrice(state.assets ? state.assets.filter(v => !v.custom_id) : [])}원</span></p>
            <div className={styles.InfoContent}>
              <div className={styles.OptionImgList}>
                {
                  state && state.assets &&
                  state.assets.map((value)=>{
                    if(!value.custom_id)
                    return (
                      <div className={styles.OptionImgBox}>
                        <div className={styles.OptionImg}>
                          <img src={value.img[0] == "h" ? value.img : "" + value.img} alt="" />
                        </div>
                        <div>
                          {/* <p>SIZE : 00x00</p> */}
                          <p>{value.price}원</p>
                        </div>
                      </div>
                    )
                  })
                }
              </div>
            </div>
          </div>
          <div className={styles.DetailInfoBox}>
            <p className={styles.DetailSubTitle}>커스텀 드로잉<span>{getTotalListPrice(state.assets ? state.assets.filter(v => v.custom_id) : [])}원</span></p>
            <div className={styles.InfoContent}>
              <div className={styles.OptionImgList}>
                {
                  state && state.assets &&
                  state.assets.map((value)=>{
                    if(value.custom_id)
                    return (
                      <div className={styles.OptionImgBox}>
                        <div className={styles.OptionImg}>
                          <img src={value.img[0] == "h" ? value.img : "" + value.img} alt="" />
                        </div>
                        <div>
                          {/* <p>SIZE : 00x00</p> */}
                          <p>{value.price}원</p>
                        </div>
                      </div>
                    )
                  })
                }
              </div>
            </div>
          </div>
          <div className={styles.PriceWrap}>
            <p>총 결제 금액 : {getTotalPrice()}원</p>
            <div>
              <Button title="CANCEL"/>
              <Button title="NEXT"/>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default OrderDetailContent;
